<template>
<header class="cabecalho" > 
    <span class="spanimg" >
        <img @click="mostrarmenu" v-if="!menu"
         src="../../assets/ico.png" alt="menu">
    </span>
    <h1 class="title">
        <span><strong>Financeiro</strong> Fno´s</span>
        {{ title }}
    </h1>
    
</header>
</template>

<script>
export default {
    name:'Cabecalho',
    props:{
        title: String,
        menu: Boolean
    },
    computed:{
        ico(){
            return "↓"
        }
    },
    methods:{
        mostrarmenu(){
            this.$store.commit('mostrarmenu')

        }
    }
}
</script>

<style>
    .cabecalho{
        grid-area: cabecalho;
        background-color: rgb(135, 135, 163);
        display: flex;
        justify-content: center;
        align-items: center;
    }   

    img{
        width: 45px;
    }

    .title{
        font-size: 1.2 rem;
        font-weight: 100;
        color: black;
        text-align: center;
        flex-grow: 1;
    }

    .spanimg{
        display: flex;
        justify-content: center;
        position: relative;
        margin:5px;
        height: 100%;
    }

    .spanimg:hover{
        background-color: rgb(86, 93, 78);

    }
</style>