<template>
  <div id="app" :class="{'mosta-menu': !mostramenu}">
      <cabecalho title="" :menu="false" /> 
      <minu />
      <conteudo />
      <rodape />
  </div>
</template>

<script>
import {mapState} from 'vuex'
import cabecalho from '@/components/template/cabecalho.vue'
import minu from '@/components/template/menu.vue'
import conteudo from '@/components/template/conteudo.vue'
import rodape from '@/components/template/rodape.vue'
//import dados from '@/components/importarDados/importadoDados.vue'

export default {
  name:'App',
  components:{cabecalho,minu, conteudo, rodape},
  computed: mapState(['mostramenu']),
  created() {
    this.resolucao()
  },
  methods: {
		async resolucao() {
      if(this.$mq === 'xs' || this.$mq === 'sm') {
					this.$store.commit('mostrarmenu', false)
				}
    }
}
}
</script>
<style>
  body{
    margin:0,
  }

  #app{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    height: 100vh;
    display: grid;
    grid-template-rows: 45px 1fr 20px;
    grid-template-columns: 200px 1fr;
    grid-template-areas:
      "cabecalho cabecalho"
      "menu conteudo"
      "menu rodape";
  }

  #app.mosta-menu{
    grid-template-areas:
      "cabecalho cabecalho"
      "conteudo conteudo"
      "rodape rodape";

  }

</style>
