import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        mostramenu:true
    },
    mutations:{
        mostrarmenu(state, visivel){
            // if(!state.user) {
            //     state.isMenuVisible = false
            //     return
            // }

            if(visivel === undefined){
                state.mostramenu =  !state.mostramenu
            }else{
                state.mostramenu = visivel
            }
        }
    }
})