<template>
  <div>
    <div class="alerta">
      <v-alert
      :value="informe"
      color="pink"
      dark
      dismissible
      border="bottom"
      type="error"
      transition="scale-transition"
    >{{alerta}}</v-alert>

      </div>
    <b-form>
      <b-row>
        <b-col md="1" sm="12">
          <b-form-group label="Empresa">
            <b-form-input placeholder="Digite aqui..."
            v-model="empresa"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Nº Contrato">
            <b-form-input placeholder="Digite aqui..."
            v-model="numeroContrato"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Modelo">
              <div  class="form-group">
                <select class="form-control btn btn-secondary" name="" id="">
                  <option>modelo1</option>
                  <option>modelo2</option>
                  <option>modelo3</option>
                </select>
              </div>
            </b-form-group>
          </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Data do Crédito">
            <b-form-input type="date" value="2020-11-18"
            v-model="datadocred"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Banco">
            <b-form-input placeholder="Digite aqui..."  
            v-model="banco"
            ></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="4" sm="12">
          <b-form-group label="Valor Financ.">
            <b-form-input placeholder="Digite aqui..."
            maxlength="15" 
            @keyup="formatarValorFinanciado"
            v-model="valorFinanciado"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Quant. de parcelas">
            <b-form-input placeholder="Digite aqui..." 
            maxlength="3" 
            @keyup="formatarNumerosParcelas"
            v-model="quantParc"></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="1" sm="12">
          <b-form-group label="Taxa">
            <b-form-input placeholder="Digite aqui..." 
            maxlength="5"
            @keyup="formatarTaxa"
            v-model="taxa"></b-form-input>
          </b-form-group>
        </b-col>
        <!-- <b-col md="2" sm="12">
          <b-form-group label="Data do crédito">
            <b-form-input placeholder="Digite aquisss..."></b-form-input>
          </b-form-group>
        </b-col> -->
        <b-col md="2" sm="12">
          <b-form-group label="Data da 1º Parc.">
            <b-form-input placeholder="Digite aqui..."></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          <b-form-group label="Carência">
            <b-form-input placeholder="Digite aqui..."></b-form-input>
          </b-form-group>
        </b-col>
        <b-col md="2" sm="12">
          
        </b-col>
        <b-col md="2" sm="12">
          <button type="button" class="btn btn-info" @click="gerar" >Gerar Contrato</button>
        </b-col>
      </b-row>
    </b-form>
  </div>
</template>

<script>
export default {
  data(){
    return{
      informe:false,
      alerta:'',
      datadocred:'',
      banco:'',
      valorFinanciado:'',
      empresa:'',
      numeroContrato:'',
      quantParc:'',
      taxa:'',
      ///////////////////////////////////////////////////
      //var valor =valorfinanc.value
      i:1,
      paras:'',
      //$tr:document.createElement('tr'),
      dyas:'',
      //dataPag:datadocred,
      NovaData:'',//new Date(this.datadocred),
      dyas2:'',
      taxamem:'',
      fin:'',
      Amort:'',
      saldo:'',
      parc:'',
      taxa1:'',
      taxaDoMes1:'',
      //////////////////////////////////////////////////  
      ar1:[],
      ar2:[],
      ar3:[],
      ar4:[],
      ar5:[],
      ar6:[],
      ar7:[],
      ar8:[],
      resultado:[]
    }
  },
  methods:{
   async gerar(){     
      // this.gerar.timer = setTimeout(() => {
      //   this.informe = true
      // }, 3000)
      console.log(this.datadocred)
      if(!this.empresa ){
        this.informe=true
        return this.alerta="Empresa não informado"
      }
      if(!this.numeroContrato ){
        this.informe=true
        return this.alerta="Contrato não informado"
      }
      if(!this.datadocred ){
        this.informe=true
        return this.alerta="Data não informado"
      }      
      if(!this.banco ){
        this.informe=true
        return this.alerta="Banco não informado"
      } 
      if(!this.valorFinanciado){
              this.informe=true
              return this.alerta="valor não informado" 
      } 
      if( this.quantParc>420){
        this.informe=true
        return this.alerta="Numero de parcelas acima do permitido"
      }
      if(!this.quantParc){
        this.informe=true
        return this.alerta="Parcela não informada"
      } 
      if(!this.taxa) {
        this.informe=true
        return this.alerta="Taxa não informada"
      } 
      this.NovaData= new Date(this.datadocred)
      this.dyas = this.NovaData
      this.informe=false
      //if(newdata.value==='') 
      //    return this.alerta="Informe uma data"
      //if(paras>0)
      //  return alert("Existre uma simulação de Fno já lançado, a pagina será recarregada"),window.location.reload()
      for(let i=0; i<this.quantParc; i++){
        this.diasDaUltimaParcela()
        this.taxaDoMesProporcional()
        this.DataDoPagamento()
        this.numeroDaParcela()
        this.amortcal()
        this.taxacal()
        this.prestcal()       
        this.saldocal()
        }
      this.ar1.forEach((item, index) => {
          this.resultado.push({ 
            id: index, 
            dias: this.ar1[index], 
            taxaMes: this.ar2[index],
            vencimento: this.ar3[index],
            Parc: this.ar4[index],
            amortização: this.ar5[index],
            juros: this.ar6[index],
            prestação: this.ar7[index],
            saldo: this.ar8[index],
            })
      })

      const dados = this.resultado
      this.$http.put(`${this.numeroContrato}.json`, { dados
      }).then(res=> console.log(res))
        
      this.resultado=[]
      },

    diasDaUltimaParcela(){
      if(this.dyas2===undefined || this.dyas === undefined || this.dyas2< 1 || this.dyas <1 ){
        this.taxamem=''
      }else{
        this.taxamem=Math.abs((this.dyas-this.dyas2)/1000/60/60/24)
      }
      console.log(`${this.i} ${this.dyas}`)
      console.log(`${this.i} ${this.dyas2}`)
      console.log(`${this.i} ${this.taxamem}`)
        this.ar1.push(this.taxamem)
      },
    taxaDoMesProporcional(){
      if(this.dyas2===undefined && this.dyas2 === undefined){
        this.taxaDoMes1=''
      }else{
        this.taxaDoMes1 = ((1+(parseFloat(this.taxa)/100))**(this.taxamem/360)-1).toFixed(5)
      }
        this.ar2.push(this.taxaDoMes1)
      },
    DataDoPagamento(){
      function cal(n){
        if(n===0)
          return 12
        return n<=9?`0${n}`:`${n}`;
      }
    
      function formatarDate(date){       
        let d = cal(date.getDate())
        let m= cal(date.getMonth())
        let a= cal(date.getFullYear())
        return `${d}-${m}-${a}`
      }
      
      let dd = this.NovaData.setMonth(this.NovaData.getMonth()+1)

      let dataLocal;
      if(this.dyas2===undefined && this.dyas2 === undefined){
        dataLocal=''
      }else{
        dataLocal=formatarDate(this.NovaData)
      }

      this.ar3.push(dataLocal)
      if(this.i%2===0 ){
          this.dyas=dd
      }else{
          this.dyas2=dd
      }
     },
    numeroDaParcela(){
      var pp;
      if(this.taxaDoMes1 === ''){
          pp=''
      }else{
          pp = `${this.i}ª`
      }
      this.ar4.push(pp)
    },
    amortcal(){
     
      this.fin = parseFloat(((this.valorFinanciado).replace(/\./gi,'')).replace(/,/,'.'))
      this.Amort = this.fin /(parseFloat(this.quantParc))
      this.saldo = this.fin-(this.Amort*this.i)
      this.taxa1 =((this.saldo+this.Amort) * parseFloat(this.taxa)/100)
      this.parc =this.Amort+this.taxa1
      let Amortvalor3=',$1'
      let AmortLocal = this.Amort
      let Amortvalor1 =function valor1(){
          if(AmortLocal>999999){            
              Amortvalor3='.$1.$2,$3'
              return /([0-9]{3})([0-9]{3}).([0-9]{2}$)/g
          }
          if(AmortLocal>999){
              Amortvalor3='.$1,$2'
              return /([0-9]{3}).([0-9]{2}$)/g
          }
          return /.([0-9]{2})$/g
      }
      let Amortvalor2=Amortvalor1()
      this.ar5.push(`${this.Amort.toFixed(2).replace(Amortvalor2,Amortvalor3)}`)   
    },
    taxacal(){
      this.fin = parseFloat(((this.valorFinanciado).replace(/\./gi,'')).replace(/,/,'.'))
      this.Amort = this.fin /parseFloat(this.quantParc)
      this.saldo = this.fin-(this.Amort*this.i)
      // taxa1 =((this.saldo+Amort) * parseFloat(taxa)/100)
      this.taxa1 =((this.saldo+this.Amort) * this.taxaDoMes1)

      this.parc =this.Amort+this.taxa1
      let taxa1valor3=',$1'
      let taxa1Local= this.taxa1
      let taxa1valor1 =function valor1(){
          if(taxa1Local>999999.99){            
              taxa1valor3='.$1.$2,$3'
              return /([0-9]{3})([0-9]{3}).([0-9]{2}$)/g
          }
          if(taxa1Local>999.99){
              taxa1valor3='.$1,$2'
              return /([0-9]{3}).([0-9]{2}$)/g
          }
          return /.([0-9]{2})$/g
      }   
      let taxa1valor2=taxa1valor1()
      this.ar6.push(`${this.taxa1.toFixed(2).replace(taxa1valor2,taxa1valor3)}`)
    },
    prestcal(){
      this.fin = parseFloat(((this.valorFinanciado).replace(/\./gi,'')).replace(/,/,'.'))
      this.Amort = this.fin /parseFloat(this.quantParc)
      this.saldo = this.fin-(this.Amort*this.i)
      this.taxa1 =((this.saldo+this.Amort) * this.taxaDoMes1)
      this.parc =this.Amort+this.taxa1
      let parcvalor3=',$1'
      let parcLocal= this.parc
      let parcvalor1 =function valor1(){
        if(parcLocal>999999.99){            
            parcvalor3='.$1.$2,$3'
            return /([0-9]{3})([0-9]{3}).([0-9]{2}$)/g
        }
        if(parcLocal>999.99){
            parcvalor3='.$1,$2'
            return /([0-9]{3}).([0-9]{2}$)/g
        }
            return /.([0-9]{2})$/g
        }   
      let parcvalor2=parcvalor1()
      this.ar7.push(`${this.parc.toFixed(2).replace(parcvalor2,parcvalor3)}`)
    },
    saldocal(){
      let saldovalor3=',$1'
      let saldoLocal= this.saldo
      let saldovalor1 =function valor1(){
        if(saldoLocal>999999.99){            
            saldovalor3='.$1.$2,$3'
            return /([0-9]{3})([0-9]{3}).([0-9]{2}$)/g
        }
        if(saldoLocal>999.99){
            saldovalor3='.$1,$2'
            return /([0-9]{3}).([0-9]{2}$)/g
        }
        return /.([0-9]{2})$/g
      }   
      let saldovalor2=saldovalor1()
      this.ar8.push(`${this.saldo.toFixed(2).replace(saldovalor2,saldovalor3)}`)
        this.i++
    },
     formatarValorFinanciado(){       
       //console.log(this.valorFinanciado)
       let valor = this.valorFinanciado;
       valor = valor + ''
       valor= parseInt(valor.replace(/\D+/g, ''))
       valor = valor + '';
       valor = valor.replace(/([0-9]{2})$/g, ",$1");
      
       if (valor.length > 9){
           valor = valor.replace(/([0-9]{3})([0-9]{3}),([0-9]{2}$)/g, ".$1.$2,$3 ");
       }else if (valor.length > 6){
           valor = valor.replace(/([0-9]{3}),([0-9]{2}$)/g, ".$1,$2");
       }
       this.valorFinanciado = valor;
       if(valor == 'NaN') this.valorFinanciado = '';
     },
    formatarNumerosParcelas(){
      let valor = this.quantParc;
      valor = valor + ''
      valor= parseInt(valor.replace(/\D+/g, ''))
      valor = valor + '';
      if(valor == 'NaN') this.quantParc = '';
      this.quantParc > 420 ? this.alerta="Numero de parcelas acima do permitido":this.alerta=""
    },
    formatarTaxa(){
      let valor = this.taxa;
      valor = valor + ''
      valor= parseInt(valor.replace(/\D+/g, ''))
      valor = valor + '';
      if(valor == 'NaN') this.taxa = '';
    }
  }
}
</script>

<style>
  .alerta{
    position: relative;
    margin-top: 0px;
    max-width: 500px;
    max-width: 500px;
    text-align: center;

    color: red;
  }
  .theme--dark.v-sheet{
    background-color: orange;
    display: true;
  }

</style>