<template>

<div data-app>
      <h5><strong>>>Contrato</strong> </h5>
     <v-col
        md="2"
        class="d-flex"
        cols="12"
        >
          <v-select
          v-model="index"
            :items="items"
            outlined
            @change="teste()"
          ></v-select>
    </v-col> 
    <v-btn
      color="blue-grey"
      class="ma-2 white--text"
      @click="importar"
    >
      Importar
      <v-icon
        right
        dark
      >
        mdi-cloud-upload
      </v-icon>
    </v-btn>
    <br>
    <br>
    <br>
  <v-data-table
    :headers="headers"
    :items="desserts"
    sort-by="Parc"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title><strong> Em fase de ajuste ainda :D</strong> </v-toolbar-title>
        
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              Nsew Item
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.vencimento"
                      label="Dessert vencimento"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.Parc"
                      label="Parc"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.amortização"
                      label="amortização"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.juros"
                      label="juros"
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="4"
                  >
                    <v-text-field
                      v-model="editedItem.prestação"
                      label="prestação"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="blue darken-1"
                text
                @click="close"
              >
                Cancel
              </v-btn>
              <v-btn
                color="blue darken-1"
                text
                @click="save"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="headline">Deseja realmente excluir?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-2" text @click="closeDelete">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        mdi-pencil
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        mdi-delete
      </v-icon>
    </template>
    <!-- <template v-slot:no-data>
      <v-btn
        color="primary"
        @click="initialize"
      >
        Reset
      </v-btn>
    </template> -->
  </v-data-table>
  </div>
</template>

<script>
  export default {
    data: () => ({
      index:[],
      items: [],
      dialog: false,
      dialogDelete: false,
      headers: [
        // {text: 'Dessert (100g serving)',align: 'start',sortable: false,value: 'vencimento',},
        // { text: 'Parc', value: 'Parc' },
        // { text: 'amortização', value: 'amortização' },
        // { text: 'juros', value: 'juros' },
        // { text: 'prestação', value: 'prestação' },
         
        {text:'vencimento', value:'vencimento'},
        {text:'Parc', value:'Parc'},
        {text:'amortização', value:'amortização', sortable: false},
        {text:'juros', value:'juros', sortable: false},
        {text:'prestação', value:'prestação', sortable: false},
        {text:'saldo', value:'saldo', sortable: false},
        //{text:'tabb', value:'editar'},
        { text: 'Actions', value: 'actions', sortable: false },

      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {
        vencimento: '',
        Parc: 0,
        amortização: 0,
        juros: 0,
        prestação: 0,
      },
      defaultItem: {
        vencimento: '',
        Parc: 0,
        amortização: 0,
        juros: 0,
        prestação: 0,
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Nova parcela' : 'Editar parcela'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },
    created () {
      //this.impa()
      this.selecaoContrato()
    },
    methods: {
      selecaoContrato(){
        console.log('idenxxxx')
             this.$http.get('.json').then(res=>{
                 this.items=Object.keys(res.data)
             })
      },
      teste(){
        console.log(this.index)
      },
      importar(){
        typeof(this.index)== 'object'?alert('Selecione um contrato'):this.importando()
      },
      importando(){
            console.log('carregando')
             this.$http.get(`${this.index}.json`).then(res=>{
                 this.desserts = res.data.dados
                 console.log('carregoouuu')
                 this.index=[]
             })
        },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },
      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },
      deleteItemConfirm () {
        this.desserts.splice(this.editedIndex, 1)
        this.closeDelete()
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
<style scoped>

</style>